#logo {
    width:100%;
    max-width:900px;
    display: block;
    margin: auto;
    color: #fff;
    font-family: Monoton;
    -webkit-animation: neon 1.5s ease-in-out infinite alternate;
    animation: neon 1.5s ease-in-out infinite alternate;
}
  
#logo:hover {
    color: #FF1177;
    -webkit-animation: none;
    animation: none;
}

@-webkit-keyframes neon {
    from {
        -webkit-filter: drop-shadow(0 0 .5em #00ffd8);
                filter: drop-shadow(0 0 .5em #00ffd8);
    }
    to {
        -webkit-filter: drop-shadow(0 0 .5em #363a39);
                filter: drop-shadow(0 0 .5em #363a39);
    }
}

@keyframes neon {
    from {
        -webkit-filter: drop-shadow(0 0 .5em #00ffd8);
                filter: drop-shadow(0 0 .5em #00ffd8);
    }
    to {
        -webkit-filter: drop-shadow(0 0 .5em #363a39);
                filter: drop-shadow(0 0 .5em #363a39);
    }
}


